.container {
    width: 110px;
    height: 185px;
    box-shadow: 0px 0px 16px rgba(0, 0, 0, 0.1);
    border-radius: 16px;
    margin: 1%;
    padding: 3%;
}

.headerPokemon{
    display: flex;
}

.settings{
    margin-left: auto;
}

.text{
    font-weight: bold;
}

.pokemonImg img{
    width: 100%;
    justify-content: center;
    align-items: center;
}

.specifications{
    display: flex;
    font-weight: bold;
    justify-content: space-between;
}